<script setup lang="ts">
import { ref, onMounted, onUnmounted, watch } from 'vue'
import { useUserProgress } from '@/composables/useUserProgress'

const props = withDefaults(defineProps<{
  totalPages: number
  pageHeight?: number // vh unit, default 100
}>(), {
  pageHeight: 100
})

const emit = defineEmits<{
  (e: 'page-change', page: number): void
}>()

const { progress, setCurrentPage } = useUserProgress()
const isScrolling = ref(false)
const containerRef = ref<HTMLElement | null>(null)

// Ensure we start at the saved page or 0
const currentPage = ref(progress.value.currentPage || 0)

const handleScroll = (event: WheelEvent) => {
  if (isScrolling.value) return

  // Basic threshold to avoid sensitive trackpads triggering too easily
  if (Math.abs(event.deltaY) < 30) return

  // Check if we are scrolling inside a scrollable element
  // Only trigger page change if we reached the top/bottom boundary of that element
  const target = event.target as HTMLElement
  const scrollable = target.closest('.overflow-y-auto')
  
  if (scrollable) {
    const el = scrollable as HTMLElement
    // If we are scrolling down and not at the bottom, don't change page
    if (event.deltaY > 0 && el.scrollTop + el.clientHeight < el.scrollHeight - 5) {
      return
    }
    // If we are scrolling up and not at the top, don't change page
    if (event.deltaY < 0 && el.scrollTop > 5) {
      return
    }
  }

  if (event.deltaY > 0) {
    nextPage()
  } else {
    prevPage()
  }
}

const nextPage = () => {
  if (currentPage.value < props.totalPages - 1) {
    scrollToPage(currentPage.value + 1)
  }
}

const prevPage = () => {
  if (currentPage.value > 0) {
    scrollToPage(currentPage.value - 1)
  }
}

const scrollToPage = (page: number) => {
  if (isScrolling.value) return
  
  isScrolling.value = true
  currentPage.value = page
  setCurrentPage(page)
  emit('page-change', page)

  // Reset scrolling lock after animation finishes
  setTimeout(() => {
    isScrolling.value = false
  }, 1000) // Match transition duration
}

// Handle keyboard navigation
const handleKeydown = (event: KeyboardEvent) => {
  if (event.key === 'ArrowDown' || event.key === 'PageDown') {
    nextPage()
  } else if (event.key === 'ArrowUp' || event.key === 'PageUp') {
    prevPage()
  }
}

onMounted(() => {
  window.addEventListener('wheel', handleScroll, { passive: false })
  window.addEventListener('keydown', handleKeydown)
})

onUnmounted(() => {
  window.removeEventListener('wheel', handleScroll)
  window.removeEventListener('keydown', handleKeydown)
})

// Expose jumpToPage for parent components (like navigation)
defineExpose({
  scrollToPage
})
</script>

<template>
  <div class="h-screen w-screen overflow-hidden bg-slate-50 text-slate-900 relative">
    <!-- The sliding container -->
    <div 
      ref="containerRef"
      class="w-full transition-transform duration-1000 ease-in-out absolute top-0 left-0"
      :style="{ transform: `translateY(calc(-${currentPage * pageHeight}vh + ${(100 - pageHeight) / 2}vh))` }"
    >
      <!-- Each page slot content -->
      <slot :current-page="currentPage"></slot>
    </div>
    
    <!-- Optional: Navigation dots/indicator -->
    <div class="fixed right-4 top-1/2 -translate-y-1/2 flex flex-col gap-2 z-50">
      <div 
        v-for="index in totalPages" 
        :key="index"
        class="w-2 h-2 rounded-full transition-all duration-300 cursor-pointer"
        :class="[
          currentPage === index - 1 ? 'bg-blue-600 scale-125' : 'bg-slate-300 hover:bg-slate-400'
        ]"
        @click="scrollToPage(index - 1)"
      ></div>
    </div>
  </div>
</template>
