<script setup lang="ts">
import { ref, onMounted, defineAsyncComponent, computed, shallowRef } from 'vue'
import { useRoute } from 'vue-router'
import FullPageScroller from '@/components/FullPageScroller.vue'
import CollapsibleNavigation from '@/components/CollapsibleNavigation.vue'
import type { HandbookConfig, PageConfig } from '@/types'

// Dynamic imports for templates
const BigTitlePage = defineAsyncComponent(() => import('@/templates/BigTitlePage.vue'))
const CardsPage = defineAsyncComponent(() => import('@/templates/CardsPage.vue'))
const MasterBulletsPage = defineAsyncComponent(() => import('@/templates/MasterBulletsPage.vue'))
const RoleMappingPage = defineAsyncComponent(() => import('@/templates/RoleMappingPage.vue'))
const LabeledPage = defineAsyncComponent(() => import('@/templates/LabeledPage.vue'))
const WordCloudPage = defineAsyncComponent(() => import('@/templates/WordCloudPage.vue'))
const ListPage = defineAsyncComponent(() => import('@/templates/ListPage.vue'))

const route = useRoute()
const config = ref<HandbookConfig | null>(null)
const currentPageIndex = ref(0)
const scrollerRef = ref()
const pageHeightVh = ref(100)

// Map page types to components
const componentMap: Record<string, any> = {
  'bigTitle': BigTitlePage,
  'cards': CardsPage,
  'master-bullets': MasterBulletsPage,
  'roleMapping': RoleMappingPage,
  'labeled': LabeledPage,
  'wordCloud': WordCloudPage,
  'list': ListPage
}

// Processed pages list (flat list of slides to be rendered)
// Note: We need to merge grouped pages into single "slide" entries for the scroller
const slides = ref<PageConfig[]>([])

// Helper to find all pages belonging to a group
const getGroupPages = (groupId: string) => {
  return config.value?.pages.filter(p => p.groupId === groupId) || []
}

onMounted(async () => {
  try {
    let data;

    // Check if loading by UUID (Public Published View)
    // The route param is defined as :uuid in router/index.ts
    const uuid = route.params.uuid as string;
    
    if (uuid) {
      console.log(`Loading published handbook: ${uuid}`);
      const apiBase = (import.meta.env.VITE_API_BASE_URL || 'http://localhost:3000').replace(/\/$/, '');
      const response = await fetch(`${apiBase}/${uuid}`);
      if (!response.ok) {
        throw new Error(`Failed to load handbook: ${response.statusText}`);
      }
      data = await response.json();
    } else {
      // Fallback to local config logic
      let product = route.query.product as string
      
      if (!product) {
        const urlParams = new URLSearchParams(window.location.search)
        product = urlParams.get('product') || ''
      }
      
      // Default to 'handbook'
      if (!product) product = 'handbook'

      // Security check: only allow alphanumeric characters to prevent path traversal
      const safeProduct = product.replace(/[^a-z0-9_-]/gi, '')
      const configUrl = `/${safeProduct}_config.json`
      
      console.log(`Loading config from: ${configUrl} (Product: ${safeProduct})`)
      
      const response = await fetch(configUrl)
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }
      data = await response.json()
    }

    config.value = data
    
    if (data.pages) {
      // Logic to merge grouped pages into single slide entries
      // We iterate through pages, if a page has a groupId, we check if we already added a slide for this group
      // If yes, skip. If no, add it.
      
      const processed: PageConfig[] = []
      const processedGroups = new Set<string>()

      data.pages.forEach((page: PageConfig) => {
        if (page.groupId) {
          if (!processedGroups.has(page.groupId)) {
            processedGroups.add(page.groupId)
            processed.push(page)
          }
        } else {
          processed.push(page)
        }
      })
      
      slides.value = processed
    }
  } catch (e) {
    console.error('Failed to load handbook config', e)
  }

  // Adjust height for mobile toolbar
  const vh = window.innerHeight * 0.01
  document.documentElement.style.setProperty('--vh', `${vh}px`)
  pageHeightVh.value = 100 // Reset to 100, let CSS handle --vh if needed, or just use 100vh
})

// Navigation Logic
const navSections = computed(() => {
  if (!config.value) return []
  
  // Group slides by topicGroup
  const groups: Record<string, { id: string, label: string, pageIndex: number }[]> = {}
  
  slides.value.forEach((slide, index) => {
    const groupName = slide.topicGroup || 'General'
    if (!groups[groupName]) {
      groups[groupName] = []
    }
    
    // For grouped slides, use the group title or first page title
    // Ideally the config should have a clean title for the group
    groups[groupName].push({
      id: slide.id,
      label: slide.showInNavShortcut ? slide.title : '', // Only show label if showInNavShortcut is true? 
      // Actually, navigation usually shows all items, but maybe filters based on flag.
      // The user requirement said: "bool, ... ,  topicGroup "
      // So we filter here.
      pageIndex: index
    })
  })

  // Convert to array format expected by CollapsibleNavigation
  return Object.entries(groups).map(([title, items]) => ({
    title,
    items: items.filter((_, idx) => {
       // We need to check the source slide for the showInNavShortcut flag
       // Since 'items' here is constructed from 'slides', we can look up the slide
       const slide = slides.value[items[idx].pageIndex]
       return slide.showInNavShortcut
    })
  })).filter(group => group.items.length > 0)
})

const totalPages = computed(() => slides.value.length)

const handlePageChange = (page: number) => {
  currentPageIndex.value = page
}

const handleNavigate = (pageIndex: number) => {
  if (scrollerRef.value) {
    scrollerRef.value.scrollToPage(pageIndex)
  }
}

const handleNavigateById = (pageId: string) => {
  const index = slides.value.findIndex(s => s.id === pageId)
  if (index !== -1) {
    handleNavigate(index)
    return
  }

  // If not found in slides, it might be a sub-page in a group.
  if (config.value) {
     const targetPage = config.value.pages.find(p => p.id === pageId)
     if (targetPage && targetPage.groupId) {
       const slideIndex = slides.value.findIndex(s => s.groupId === targetPage.groupId)
       if (slideIndex !== -1) {
         handleNavigate(slideIndex)
         // Wait for scroll to complete or simply update config immediately
         // We need to ensure the slide is active before updating its config?
         // No, updating config should be fine.
         updateSlideConfig(pageId)
       }
     }
  }
}

// Special handler for LabeledPage internal navigation (switching tabs)
// When LabeledPage emits 'navigate-to-page', it means user clicked a tab.
// We need to update the "current config" for that slide to reflect the new active page in the group.
// Wait, `slides` array holds the *initial* page of the group. 
// If we want to change the content, we should update the entry in `slides`.
const updateSlideConfig = (pageId: string) => {
   const targetPage = config.value?.pages.find(p => p.id === pageId)
   if (!targetPage || !targetPage.groupId) return

   const slideIndex = slides.value.findIndex(s => s.groupId === targetPage.groupId)
   if (slideIndex !== -1) {
     // Reactive update
     slides.value[slideIndex] = targetPage
   }
}

// Handle WordCloud jump
const handleJumpToPageNumber = (pageNumber: number) => {
  // The JSON uses pageNumber (1-based), but our slides are 0-based index.
  // Also, pageNumber in JSON might correspond to logical pages, not necessarily slide index.
  // User requirement: " pageNumber... ... "
  // Let's find the slide that has this pageNumber
  const slideIndex = slides.value.findIndex(s => s.pageNumber === pageNumber)
  if (slideIndex !== -1) {
    handleNavigate(slideIndex)
  }
}

</script>

<template>
  <div class="h-[100vh] w-full overflow-hidden flex bg-slate-50 font-sans text-slate-900">
    <!-- Sidebar Navigation -->
    <CollapsibleNavigation 
      :sections="navSections"
      :current-page="currentPageIndex"
      :total-pages="totalPages"
      @navigate="handleNavigate"
    />

    <!-- Main Content Area -->
    <main class="flex-1 h-full relative">
      <FullPageScroller 
        v-if="slides.length > 0"
        ref="scrollerRef" 
        :total-pages="totalPages" 
        :page-height="70"
        @page-change="handlePageChange"
      >
        <template #default="{ currentPage }">
          <div 
            v-for="(slide, index) in slides" 
            :key="slide.groupId || slide.id"
            class="w-full"
            :style="{ height: '70vh' }"
          >
            <!-- Render template based on pageType -->
            <component 
              :is="componentMap[slide.pageType]"
              v-if="componentMap[slide.pageType]"
              :config="slide"
              :group-pages="slide.groupId ? getGroupPages(slide.groupId) : []"
              @navigate-to-page="updateSlideConfig"
              @jump-to="handleJumpToPageNumber"
            />
            
            <!-- Fallback for unknown type -->
            <div v-else class="h-full flex items-center justify-center">
              <p class="text-red-500">Unknown page type: {{ slide.pageType }}</p>
            </div>
          </div>
        </template>
      </FullPageScroller>
      
      <div v-else class="h-full flex items-center justify-center">
        <p class="text-slate-400">Loading handbook...</p>
      </div>
    </main>
  </div>
</template>