import { createRouter, createWebHashHistory } from 'vue-router'
import HomePage from '@/pages/HomePage.vue'

// Website Pages
const LandingPage = () => import('@/pages/website/LandingPage.vue')

// Admin/CMS Pages
const LoginPage = () => import('@/pages/cms/LoginPage.vue')
const RegisterPage = () => import('@/pages/cms/RegisterPage.vue')
const DashboardPage = () => import('@/pages/cms/DashboardPage.vue')
const AdminUsersPage = () => import('@/pages/cms/AdminUsersPage.vue')
const EditorPage = () => import('@/pages/cms/EditorPage.vue')
const TemplatesPage = () => import('@/pages/cms/TemplatesPage.vue')
const PagesGalleryPage = () => import('@/pages/cms/PagesGalleryPage.vue')
const HandbooksPage = () => import('@/pages/cms/HandbooksPage.vue')
const ProfilePage = () => import('@/pages/cms/ProfilePage.vue')

// 
const routes = [
  {
    path: '/',
    name: 'home',
    component: LandingPage,
  },
  // Public Handbook View by UUID
  {
    path: '/:uuid([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})',
    name: 'handbook-view',
    component: HomePage,
    props: true
  },
  // CMS Routes
  {
    path: '/admin',
    name: 'admin-login',
    component: LoginPage
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterPage
  },
    // Manager Routes
  {
    path: '/dashboard',
    name: 'manager-dashboard',
    component: DashboardPage,
    meta: { requiresAuth: true, role: 'manager' }
  },
  {
    path: '/templates',
    name: 'manager-templates',
    component: TemplatesPage,
    meta: { requiresAuth: true, role: 'manager' }
  },
  {
    path: '/pages',
    name: 'manager-pages',
    component: PagesGalleryPage,
    meta: { requiresAuth: true, role: 'manager' }
  },
  {
    path: '/handbooks',
    name: 'manager-handbooks',
    component: HandbooksPage,
    meta: { requiresAuth: true, role: 'manager' }
  },
  {
    path: '/profile',
    name: 'manager-profile',
    component: ProfilePage,
    meta: { requiresAuth: true, role: 'manager' }
  },
  // Admin Routes
  {
    path: '/admin/dashboard',
    name: 'admin-dashboard',
    component: DashboardPage, // Or separate AdminDashboard
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/admin/users',
    name: 'admin-users',
    component: AdminUsersPage,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/admin/editor',
    name: 'editor',
    component: EditorPage,
    meta: { requiresAuth: true }
  },
  {
    path: '/about',
    name: 'about',
    component: {
      template: '<div class="text-center text-xl p-8">About Page - Coming Soon</div>',
    },
  },
]

// 
const router = createRouter({
  history: createWebHashHistory(),
  routes,
})

// Simple Auth Guard
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token');
  const userRole = localStorage.getItem('role');

  // Admin Routes Protection
  if (to.path.startsWith('/admin')) {
    if (to.name === 'admin-login') {
      if (token && userRole === 'admin') return next('/admin/dashboard');
      if (token && userRole === 'manager') return next('/dashboard');
      return next();
    }
    
    // If accessing protected /admin/* but no token or not admin
    if (!token || userRole !== 'admin') {
      return next('/admin'); // Redirect to admin login
    }
  }

  // Manager/Common Routes Protection
  if (to.meta.requiresAuth) {
    if (!token) {
      // If accessing manager routes but not logged in, go to generic login
      return next('/login');
    }
    
    // Role check for specific routes
    if (to.meta.role && to.meta.role !== userRole) {
       // If manager tries to access admin, they get kicked to their dashboard
       if (userRole === 'manager') return next('/dashboard');
       // If admin tries to access manager route (if we allow that? prompt says manager cannot access /admin, implies separation)
       // Let's keep them separated as requested
       if (userRole === 'admin') return next('/admin/dashboard');
    }
  }

  next();
});

export default router
