import { useStorage } from '@vueuse/core'

export interface UserProgress {
  currentPage: number
  completedPages: number[]
  productSelection: string | null
  quizAnswers: Record<number, string>
  lastVisitTime: string
}

export const useUserProgress = () => {
  const progress = useStorage<UserProgress>('appcharge-onboarding-progress', {
    currentPage: 0,
    completedPages: [],
    productSelection: null,
    quizAnswers: {},
    lastVisitTime: new Date().toISOString()
  })

  const setCurrentPage = (page: number) => {
    progress.value.currentPage = page
    progress.value.lastVisitTime = new Date().toISOString()
    if (!progress.value.completedPages.includes(page)) {
      progress.value.completedPages.push(page)
    }
  }

  const setProductSelection = (selection: string) => {
    progress.value.productSelection = selection
  }

  const setQuizAnswer = (questionId: number, answer: string) => {
    progress.value.quizAnswers[questionId] = answer
  }

  return {
    progress,
    setCurrentPage,
    setProductSelection,
    setQuizAnswer
  }
}
