<script setup lang="ts">
import { ref, computed, watch } from 'vue'
import { Menu, CheckCircle2, Circle, CheckCircle } from 'lucide-vue-next'

interface NavSection {
  title: string
  items: NavItem[]
}

interface NavItem {
  id: string
  label: string
  pageIndex: number
}

const props = defineProps<{
  sections: NavSection[]
  currentPage: number
  collapseThreshold?: number
  totalPages: number
}>()

const emit = defineEmits<{
  (e: 'navigate', pageIndex: number): void
}>()

const isHovered = ref(false)

// Auto-collapse if we are past the threshold (default page 1)
const shouldCollapse = computed(() => {
  const threshold = props.collapseThreshold ?? 1
  return props.currentPage >= threshold
})

const isExpanded = computed(() => {
  return isHovered.value || !shouldCollapse.value
})

const handleNavigate = (index: number) => {
  emit('navigate', index)
}
</script>

<template>
  <div 
    class="fixed top-8 left-8 z-50 transition-all duration-300 ease-in-out"
    @mouseenter="isHovered = true"
    @mouseleave="isHovered = false"
  >
    <!-- Collapsed State Icon -->
    <div 
      v-if="!isExpanded"
      class="bg-white p-3 rounded-full shadow-lg border border-slate-200 cursor-pointer hover:bg-slate-50 transition-colors"
    >
      <Menu class="w-6 h-6 text-slate-700" />
    </div>

    <!-- Expanded Menu -->
    <div 
      v-else
      class="bg-white/95 backdrop-blur-sm rounded-xl shadow-xl border border-slate-200 overflow-hidden w-64 transition-all duration-300"
    >
      <div class="p-4 border-b border-slate-100 bg-slate-50/50">
        <h3 class="font-semibold text-slate-800">Navigation</h3>
      </div>
      
      <div class="max-h-[60vh] overflow-y-auto py-2">
        <div v-for="(section, sIndex) in sections" :key="sIndex" class="mb-2 last:mb-0">
          <div class="px-4 py-1 text-xs font-bold text-slate-400 uppercase tracking-wider">
            {{ section.title }}
          </div>
          <button
            v-for="item in section.items"
            :key="item.id"
            @click="handleNavigate(item.pageIndex)"
            class="w-full px-4 py-2 flex items-center gap-3 text-left hover:bg-slate-50 transition-colors group"
            :class="currentPage === item.pageIndex ? 'bg-blue-50/50' : ''"
          >
            <!-- Status Icon -->
            <div class="shrink-0">
              <CheckCircle 
                v-if="currentPage > item.pageIndex" 
                class="w-4 h-4 text-green-500" 
              />
              <CheckCircle2 
                v-else-if="currentPage === item.pageIndex" 
                class="w-4 h-4 text-blue-600 animate-pulse" 
              />
              <Circle 
                v-else 
                class="w-4 h-4 text-slate-300 group-hover:text-slate-400" 
              />
            </div>
            
            <span 
              class="text-sm font-medium transition-colors"
              :class="currentPage === item.pageIndex ? 'text-blue-700' : 'text-slate-600'"
            >
              {{ item.label }}
            </span>
          </button>
        </div>
      </div>
      
      <!-- Current Page Indicator Footer -->
      <div class="p-3 bg-slate-50 border-t border-slate-100 text-xs text-slate-500 text-center">
        Page {{ currentPage + 1 }} of {{ totalPages }}
      </div>
    </div>
  </div>
</template>
